body {
    margin: 0;
    font-family: 'GothamVnu-Book', sans-serif;
    font-size: 16px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}


@font-face {
    font-family: "GothamVnu-Bold";
    src: url('/assets/fonts/GothamVnu-Bold.otf');
    font-weight: bold;
}

@font-face {
    font-family: "GothamVnu-Book.otf";
    src: url('/assets/fonts/GothamVnu-Book.otf');
}

.title {
    font-family: 'GothamVnu-Bold';
    font-weight: bold;
    font-size: 30px;
    text-align: center;
    color: #22485D;
}

.arrow-down {
    width: 0;
    height: 0;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-top: 10px solid #fff;
    display: inline-block;
    margin-left: 5px;
}

.has-sub-menu {
    position: relative;
}

.has-sub-menu:hover .sub-menu {
    display: block;
    position: absolute;
    margin-top: 5px;
    border-radius: 5px;
    padding: 0;
    min-width: 200px;
    background-color: #fff;
    list-style: none;
    box-shadow: 0 0 10px rgba(0,0,0,0.2);
}

.sub-menu {
    display: none;
}

.sub-menu li {
    font-size: 14px;
}

.sub-menu li a {
    padding: 15px 20px;
    display: block;
    text-decoration: none;
    color: #6EBCC8;
}

.sub-menu li a:hover {
    color: #22485D;
    background-color: rgba(34 ,72 ,93 ,0.05);
}

.loader {
    border: 6px solid #f3f3f3;
    border-top: 6px solid #6EBCC8;
    border-radius: 50%;
    width: 24px;
    height: 24px;
    animation: spin 2s linear infinite;
    display: inline-block;
    margin-left: 10px;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.roadmap-content-wrapper {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    width: 100%;
}

.roadmap-content {
    padding: 20px;
    border-radius: 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex: 0 0 60%;
    position: relative;
}

@media (max-width: 1024px) {
    .title {
        font-size: 26px;
    }

    .content-wrapper {
        margin: 0 auto;
        width: 90%;
    }

    .roadmap-content-wrapper {
        width: 90%;
    }
}

@media (max-width: 768px) {
    .title {
        font-size: 22px;
    }

    .roadmap-content-wrapper {
        width: 96%;
    }
}

@media (max-width: 576px) {
    .title {
        font-size: 18px;
    }

    .content-wrapper {
        margin: 0 auto;
        width: auto;
        max-width: 90%;
    }
}